<template>
    <div>
        <div class="chart-qr">
          
          <ProgressBar
            v-for="answer in questionsQR.answers"
            :key="answer.id"
            :progressName = answer.name
            :answerVotes = answerVotes(answer.id)
            :summaryVotes = summaryVotes
          />
          
          <!-- <div
            v-for="answer in questionsQR.answers"
            :key="answer.id"
          >
            <div>{{answer.name}} ({{ answerVotes(answer.id) }} человек) всего {{ summaryVotes }}</div>
            <div class="progress">
              <div class="progress-bar" :style="{'width': (this.summaryVotes / this.answerVotes(answer.id)*100) + '%'}"></div>
            </div>          

          </div> -->
        </div>
    </div>
</template>
  
<script>
import { mapGetters } from 'vuex'
import ProgressBar from "@/components/ProgressBar.vue"

export default {
    name: 'ChartQR',
    data() {
      return {
        sessionInfo: {}
      }
    },
    components: {
      ProgressBar
    },
    computed: {
      ...mapGetters([
          "sessionQR",
          "questionsQR"
      ]),
      summaryVotes(){
        if (this.sessionInfo) {
          const result = this.sessionInfo.result?.reduce( ( sum, { votes } ) => sum + votes , 0)
          return result
        }
        else{
          return 0
        }
      },
      // progressWidth(){
      //   return {width: this.summaryVotes / this.answerVotes(answer.id)}
      // }
    },
    methods: {
        answerVotes(answerId){
          const answerVotes = this.sessionInfo.result?.find( e => e.id === answerId)
          if (answerVotes) {
            return answerVotes.votes
          }          
        },
               
        async getVotes(session) {
          try {
            const rawResponse = await fetch(
              // `https://pitech.me/medicine/api/v1/voting/results?id=${session}`,
              // `http://docker1.med-game.ru/medicine/api/v1/voting/results?id=${session}`,
              `https://mproqrservice.ru/medicine/api/v1/voting/results?id=${session}`,              
              {
                method: "GET",
                // mode: "no-cors",
                headers: {
                  Accept: "application/json",
                  "Content-Type": "application/json",
                },
              }
            );
            return await rawResponse.json();
          } catch (e) {
            return { error: 1 };
          }
        },

        async getSessionInfo(){
          const votes = await this.getVotes(this.sessionQR)
          this.sessionInfo = votes
          // console.log(votes);
        }
      
    },
    mounted() {
        
      this.getSessionInfo()

    }
}
</script>
  
<style>
  .chart-qr{
    padding-top: 70px;
    padding-bottom: 120px;
  }
</style>
  
