<template>
    <!-- <h1 class="text-center text-red-600 font-inter-medium text-lg py-6 border-b border-slate-700"> -->
    <h1 
        :style="textColor"    
        class="main-title"        
    >
    {{titleText}}
  </h1>
</template>

<script>
export default {
    name: 'GameTitle',
    props: {
        titleText: String,
        titleColor: String
    },
    computed:{
        textColor(){
            if (this.titleColor) {
                return {"color": this.titleColor}
            }
            else{
                return ""
            }
        }        
    }
}
</script>

