<template>
    <div>            

        <div class="progress-text">
            {{progressName}} ({{ answerVotes }} человек)
        </div>

        <div class="progress">
            <div class="progress-bar" :style="progressWidth"></div>
        </div>   

    </div>
</template>
  
<script>

export default {
    name: 'ProgressBar',
    props: {
        progressName: String,
        answerVotes: Number,
        summaryVotes: Number
    },
    computed: {
        progressWidth(){
            if (this.answerVotes === 0) {
                return { "width": 0}
            }
            else{
                return { "width": ( this.answerVotes / this.summaryVotes * 100) + "%"}
            }
        }
    }
}
</script>
  
<style>
    .progress-text{
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 27px;
        color: #000000;
        margin-bottom: 6px;
    }
    .progress{
        height: 30px;
        max-width: 1240px;
        width: 100%;
        border: 1px solid #000000;
        margin-bottom: 20px;
    }
    .progress-bar{
        height: 30px;
        background: #2797D3;
    }
    
</style>
  