<template>
    <div
    @click="openSection()"
    class="section-item"
    >
        <!-- class=" hover:scale-105 duration-300 w-full min-h-[140px] h-full flex flex-col items-center justify-between shadow-md border-2 border-slate-700 rounded-md" -->
        <!-- <button 
        @click="openSection()"
        class="section-button">
            <img :src="buttonImage" class="p-2 max-w-[80px]" alt="button image">
            <div class="h-[50px]">
                <span>{{buttonName}}</span>
            </div>
        </button> -->

        <div class="section-image-container">
            <img :src="buttonImage" class="section-image" alt="button image">
        </div>
        <div class="section-text">
            {{buttonName}}
        </div>

    </div>
</template>

<script>

export default {
    name: "SectionButton",    
    props: {
        sectionItem: {},
        numberItem: Number
    },
    computed: {
        buttonImage() {
            return require(`@/assets/images/${this.sectionItem.image}`)
        },
        buttonName() {
            return this.sectionItem.sectionName
        }
    },
    methods: {
        openSection(){
            this.$emit('openSection', this.numberItem)
        }
    }
}
</script>

