<template>
    <!-- <button -->
    <div
      class="footer-button"
      @click="buttonClick"
    >
      <div class="footer-button-text" >{{buttonData.name}}</div>

    </div>
    <!-- </button> -->
</template>

<script>
export default {
    name: 'FooterButton',
    props: {
      buttonData: {}
    },
    computed: {
        
    },
    methods: {
      buttonClick(){
        this.$emit('buttonClick', this.buttonData.action)
      }
    }
}
</script>

