<template>
    <main class="game-main" :style="{'height': windowHeight+'px'}">
    <div class="main-wrapper">
        <div
            :style="borderStyle"
            class="game-view"
         >
  
        <GameHeader
        
        />

        <InfoContent
        v-if="actualView.type === 'info'"
        />

        <GameSections
        v-if="actualView.type === 'sections'"
        />

        <AnswersButtons
        v-if="actualView.type === 'answersButtons'"
        />

        <ManyAnswersButtons
        v-if="actualView.type === 'manyAnswersButtons'"
        />

        <AnswersCheckboxes
        v-if="actualView.type === 'answersCheckboxes'"
        />

        <GameFooter 

        />

        <div class="medicineCode">CMX-RU-000944 30012023</div>

      </div>      
    </div>
  </main>
    
</template>
  
<script>
import localTestData from '@/data/data2.json'
import { mapGetters, mapActions } from 'vuex'
import InfoContent from '@/components/InfoContent.vue'
import GameSections from '@/components/GameSections.vue'
import AnswersButtons from '@/components/AnswersButtons.vue'
import ManyAnswersButtons from '@/components/ManyAnswersButtons.vue'
import AnswersCheckboxes from '@/components/AnswersCheckboxes.vue'
import GameHeader from '@/components/GameHeader.vue'
import GameFooter from '@/components/GameFooter.vue'
  
export default {
    name: 'GameMain',
    components: {
        InfoContent,
        GameSections,
        AnswersButtons,
        ManyAnswersButtons,
        AnswersCheckboxes,
        GameHeader,
        GameFooter
    },
    computed: {
        ...mapGetters([
            'gameData',
            'stepNumber',
            'actualView',
            'windowHeight'
        ]),
        borderStyle(){
            if (this.actualView.borderStyle) {
                return this.actualView.borderStyle
            }
            else{
                return ""
            }
        }

        // showParams() {
        //     return this.$route.params
        // }        
    },
    methods: {
        ...mapActions([
            'GET_DATA_FROM_API',
            'GET_DATA_FROM_LOCAL',
            'setWindowHeight'
        ]),
        onResize() {
            this.setWindowHeight(window.innerHeight)
        }
    },
    created(){
        // console.log(this.showParams.idTest)
        // this.GET_DATA_FROM_API(11)
        this.GET_DATA_FROM_LOCAL(localTestData)
    },
    mounted() {
        this.$nextTick(() => {
            window.addEventListener('resize', this.onResize);
        })
    },
    beforeUnmount() { 
        window.removeEventListener('resize', this.onResize);
    },

}
</script>

<style>
    .medicineCode{
        display: flex;
        justify-content: end;
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 19px;
        text-align: right;
        color: #FFFFFF;
        position: absolute;
        bottom: -25px;
        right: 0;
    }
</style>