<template>
    <button
      @click="buttonClick"
      class="long-button">
        {{ buttonData.name }}
    </button>
</template>

<script>
import { mapActions } from 'vuex'

export default {
    name: 'LongButton',
    props: {
        buttonData: {}
    },
    methods: {
      ...mapActions([
        "doAction"
      ]),
      buttonClick(){
        this.doAction(this.buttonData.action)
      }
    },
}
</script>

<style>
  .long-button{
    width: 640px;
    height: 50px;
    background: white;
    margin-top: 13px;
    margin-bottom: 13px;
    border: 2px solid #224A81;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    text-align: center;
    color: #224A81;
    cursor: pointer;
  }
</style>