<template>
    <div class="qr-wrapper">
        <div ref="qrelement" class="qr-element">

        </div>
    </div>
</template>
  
<script>
import { mapActions } from 'vuex'

export default {
    name: 'InfoQR',
    props: {
        contentQR: {}
    },
    methods: {
        ...mapActions([
          "setSessionQR",
          "setQuestionsQR"
        ]),
        
        async sendVoteQuestions(questionsObject) {
        try {
          const rawResponse = await fetch(
            // "https://pitech.me/medicine/api/v1/voting/qrcode",
            // "http://docker1.med-game.ru/medicine/api/v1/voting/qrcode",
            "https://mproqrservice.ru/medicine/api/v1/voting/qrcode",
            {
              method: "POST",
              // mode: "no-cors",
              headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
              },
              body: JSON.stringify(questionsObject),
            }
          );
          return await rawResponse.json();
        } catch (e) {
          return { error: 1 };
        }
      },

      async getQR(questionsObj) {
        const { qrcode, session, error } = await this.sendVoteQuestions(questionsObj);
        this.setSessionQR(session),
        this.setQuestionsQR(this.contentQR.questionsObject)
        
        const bodyBlock = this.$refs.qrelement
        bodyBlock.innerHTML = "";

        if (error) {
          bodyBlock.innerHTML =
            "<h2>Нет связи с сервером :( </h2><div>Нажмите далее для продолжения<div>";
        } else {
          var image = new Image();
          image.src = qrcode;
          image.style.width = "99%"
          bodyBlock.appendChild(image);          
        }
      }
    },
    mounted() {
        this.getQR(this.contentQR.questionsObject)
    },
}
</script>
  
<style>
    .qr-wrapper{
        display: flex;
        justify-content: center;
    }
    .qr-element{
        width: 420px;
        height: 420px;
    }
</style>
  
